<template>
	<div>
		<CCard>
			<CCardHeader>
				<b>Edit</b> Request Barang
			</CCardHeader>
			<CCardBody>
				<CForm class="mt-4">
					<CRow class="mx-auto d-flex justify-content-center">
						<CCol sm="8">
							<CInput
								type="date"
								label="Tanggal"
								horizontal
								v-model="request_po.date"
								:description="validator.date_msg"
		                        :is-valid="validator.date"
							/>
							<CInput
								type="number"
								label="Quantity"
								horizontal
								v-model="request_po.qty"
								:description="validator.qty_msg"
								:is-valid="validator.qty"
							/>
							<CTextarea
								label="Keterangan"
								:description="validator.keterangan_msg"
								:is-valid="validator.keterangan"
								horizontal
								rows="3"
								v-model="request_po.keterangan"
							/>
							<CSelect
								label="Status"
								horizontal
								v-model="statusselected"
								:options="status"
								@update:value="assignStatus"
							/>

						</CCol>
					</CRow>
				</CForm>
			</CCardBody>

			<CCardFooter class="d-flex justify-content-center mt-4">
				<CButton type="submit" color="primary" @click.prevent="updateRequestPO">
					{{label}}
				</CButton>
			</CCardFooter>
		</CCard>
	</div>
</template>
<script type="text/javascript">
	export default {
		name: 'EditRequestPO',
		data() {
			return {
				statusselected:4,
				status:[
					{
						value:1,
						label:'Request'
						
					},
					{
						value:2,
						label:'Proses'
						
					},
					{
						value:3,
						label: 'Dikirim'
						
					},
					{
						value:4,
						label: 'Selesai'
						
					},
					{
						value:5,
						label: 'Batal'
						
					}
				],
				errors: [],
				label: 'Update',
				validator: {
					date:null,
					qty:null,
					keterangan:null,
					date_msg:null,
					qty_msg:null,
					keterangan_msg:null
				},
				request_po: {
					date:null,
					qty:null,
					keterangan:null,
					status:null,
				},
				
			}
		},
		methods: {
			getData() {
				let headers = new Headers()
				headers.append('Authorization', 'bearer ' + localStorage.token)
				let options = {
					method:'POST',
					headers,
					redirect:'follow'
				}

				fetch('https://young-temple-67589.herokuapp.com/api/request/barang/' + this.$route.params.id, options)
				.then(res => res.json())
				.then(res => {
					if(res.errcode == 40001) {
						
						this.$store.dispatch('logout')
		                  .then(() => {
		                    let path = window.location.href
		                    path = path.split('/')
		                    localStorage.setItem('prevPath', path[path.length - 1])
		                    alert('Session Login kamu sudah habis! silahkan login kembali')
		                    
		                  })
		                  .then(() => {
		                    this.$router.replace({path: '/login'})
		                  })
		                  .catch(e => {
		                    alert('An error occured when get data :(')
		                    return false
		                  })
						return false
					}
					console.log(res.status)
					this.statusselected = Number(res.status)
					this.request_po = res
					
				})
				.catch(e => {
					alert('Tidak bisa mengambil data, hubungi pengembangnya yah')
					console.error(e)
					return false
				})
			},
			assignStatus(val) {
				this.request_po.status = val
				console.log(this.request_po.status)
			},
			updateRequestPO() {
				this.label = 'Loading...'
				this.errors = []

				if(!this.request_po.date) {
					this.validator.date = false
		            this.validator.date_msg = 'Harap isi tanggal'
		            this.errors.push('date karyawan kosong')
				}

				if(!this.request_po.qty) {
					this.validator.qty = false
		            this.validator.qty_msg = 'Harap isi kuantitas barang'
		            this.errors.push('qty karyawan kosong')
				}

				if(!this.request_po.keterangan) {
					this.validator.keterangan = false
		            this.validator.keterangan_msg = 'Harap isi keterangan'
		            this.errors.push('keterangan karyawan kosong')
				}

				if(!this.errors.length) {
					this.$http.post('https://young-temple-67589.herokuapp.com/api/request/barang/edit/' + this.$route.params.id, this.request_po, {
						headers: {
							'Authorization': 'bearer ' + localStorage.token
						},
						redirect: 'follow'
					})
					.then(res => {
						this.label = 'Update Data'
						console.log(res)
						alert('Data berhasil diupdate')
						this.$router.push('/po/data-request-barang')
					})
					.catch(e => console.error(e))
				}
				else {
					console.log(this.errors)
	         		window.scrollBy({ 
		              top: -500, // could be negative value
		              left: 0, 
		              behavior: 'smooth' 
		            })
		            return false
				}
			}
		},
		created() {
			this.getData()
		}
	}
</script>